import config from './engagement-create.config.json'
import JsonViewer from '@/components/JsonViewer'
import { required } from 'vuelidate/lib/validators'
import VueNumeric from 'vue-numeric'
export default {
    name: 'engagement-create',
    data() {
        return {
            config,
            dmp: {
                id: this.$store.getters.getUid,
                username: this.$store.getters.getUsername,
                feeSharing: 0
            },
            lawyersOption: [],
            lawyersSelected: [],
            engagement: {
                clientName: '',
                address: '',
                npwp: '',
                pic: '',
                professionalFee: 0,
                caseOverview: '',
                description: '',
                notes: '',
                panitera: '',
                strategy: ''
            },
            errorMessage: ''
        }
    },
    validations: {
        engagement: {
            clientName: { required },
            address: { required },
            npwp: { required },
            pic: { required },
            professionalFee: { required },
            caseOverview: { required }
        }
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        getLawyers() {
            this.$http({
                method: 'get',
                url: '/employee/role?param=lawyer',
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                let rLawyer = response.data.filter(lawyer => lawyer.role_name == 'lawyer')
                let i = 0
                for (i==0; i < rLawyer.length; i++) {
                    let lawyer = { 
                        item: { 
                            name: rLawyer[i].name, 
                            id: rLawyer[i].employee_id
                        }, 
                        name: rLawyer[i].name }
                    this.lawyersOption.push(lawyer)
                }
            })
            .catch(error => {
                this.errorMessage = error.response.data
            })
        },
        handleSubmit() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.errorMessage = 'Please complete mandatory fields'
            } else {
                this.$bvModal.show('modal-confirmation')
            }
        },
        handleModalConfirmationOk() {
            this.$bvModal.show('modal-loading')
            // Custom employee id, name, fee
            let employeeId = []
            let employeeName = []
            let feeShare = []
            let i = 0
            for (i==0; i < this.lawyersSelected.length; i++) {
                employeeId.push(this.lawyersSelected[i].id)
                employeeName.push(this.lawyersSelected[i].name)
                feeShare.push(this.lawyersSelected[i].feeSharing)
            }
            setTimeout(() => {
                this.$http({
                    method: 'post',
                    url: '/engagement/manage-engagement',
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken
                    },
                    data: {
                        dmp_fee: this.dmp.feeSharing,
                        client_name: this.engagement.clientName,
                        address: this.engagement.address,
                        npwp: this.engagement.npwp,
                        pic: this.engagement.pic,
                        profesional_fee: this.engagement.professionalFee,
                        case_over_view: this.engagement.caseOverview,
                        description: this.engagement.description,
                        notes: this.engagement.notes,
                        panitera: this.engagement.panitera,
                        strategy: this.engagement.strategy,
                        employee_id: employeeId,
                        employee_name: employeeName,
                        fee_share: feeShare
                    }
                })
                .then(response => {
                    console.log(response.data)
                    this.$bvModal.hide('modal-loading')
                    response.data.response_code == '00' ? this.$router.push('/admin/engagement') : this.errorMessage = response.data.response
                })
                .catch(error => {
                    console.log(error.response.data)
                    this.errorMessage = error.response.data
                    this.$bvModal.hide('modal-loading')
                })
            }, 1000)
        }
    },
    created() {
        this.getLawyers()
    }
}